<template>
  <div id="addBlog">
    <h2>添加博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required>
      <label >博客内容</label>
      <textarea name="" v-model="blog.content"></textarea>

      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label>React.js</label>
        <input type="checkbox" value="React" v-model="blog.categories">

      </div>
      <label >作者：</label>
      <select v-model="blog.author">
        <option  v-for="author in authors">
          {{author}}
        </option>
      </select>
      <button v-on:click.prevent="post">添加博客</button>
    </form>
    <div v-if="submmited">
       <h3>您的博客发布成功</h3>
    </div>
    <div id="preview" >
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p>{{blog.content}}</p>
      <p>博客分类:</p>
      <ul>
        <li v-for="category in blog.categories">
          {{category}}
        </li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'addBlog',
    data () {
      return {
          blog:{
            title:'',
            content:'',
            categories:[],
            author:''
          },
        authors:['kebo','james', 'curry','allen-wu'],
        submmited:false,
      }
    },
    methods:{
        post:function () {
          this.$http.post('https://vue-blog-e72a8.firebaseio.com/posts.json', this.blog)
            .then(function (data) {

              this.submmited = true;
            })
        }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#addBlog *{
  box-sizing: border-box;
}
  #addBlog{
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
  }
  label{
    display: block;
    margin: 20px 0px 10px;
  }
  input[type='text'],textarea,select{
    display: block;
    width: 100%;
    padding: 8px;
  }
  textarea{
    height: 200px;
  }
  #checkboxes label{
    display: inline-block;
    margin-top: 0;
  }
  button{
    display: block;
    margin: 20px 0;
    background: crimson;
    color: white;
    border: 0;
    padding: 14px;
    border-radius:5px ;
    font-size: 18px;
    cursor: pointer;

  }

  #preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
  }
  h3{
    margin-top: 10px;
  }
</style>



